namespace $.$$ {

	export class $bun_alh_game_cell_result extends $.$bun_alh_game_cell_result {
		
		@ $mol_mem
		text() {
			var empty_count =
				this.combine_list().filter( elem => elem === null ).length
			
			if( empty_count >= 2 ) {
				return `  ${ empty_count } !`
			}
			else if( empty_count === 1 ) {
				return `  ${ empty_count } !`
			}
			else {
				return '...'
			}
		}

		@ $mol_mem
		hint() {
			return this.useless()
				? '    '
				: this.element_name()
		}

	}

}
